import React, { useState } from 'react';

function Login({ onLogin, onRegisterClick }) {
  const [email, setEmail] = useState(''); // Changed username to email
  const [password, setPassword] = useState('');

  async function handleSubmit(event) {
    event.preventDefault();

    const response = await fetch('http://localhost:80/users/login', {
  method: 'POST',
  headers: {
    'Content-Type': 'application/json'
  },
  body: JSON.stringify({ email, password })
});

if (response.ok) {
  const data = await response.json();
  console.log('Status:', data.status);
  console.log('User:', data.user);
  console.log('Message:', data.message);
} else {
  console.error('Login failed');
}
  };

  return (
    <div>
      <h2>Login</h2>
      <form onSubmit={handleSubmit}>
        <label>
          Email: {/* Changed Username to Email */}
          <input type="email" value={email} onChange={(e) => setEmail(e.target.value)} /> {/* Changed type to email and username to email */}
        </label>
        <label>
          Password:
          <input type="password" value={password} onChange={(e) => setPassword(e.target.value)} />
        </label>
        <button type="submit">Login</button>
        <button type="button" onClick={onRegisterClick}>Create Account</button>
      </form>
    </div>
  );
}

export default Login;