import React, { useState } from 'react';

const Register = ({ onClose }) => {
  const [username, setUsername] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const handleSubmit = async (event) => {
    event.preventDefault();
  
    try {
      const response = await fetch('http://localhost:80/register', {
  method: 'POST',
  headers: {
    'Content-Type': 'application/json',
  },
  body: JSON.stringify({ username: username, email: email, password: password }),
});

if (!response.ok) {
  const data = await response.json();
  alert(data.message); // Display the error message in an alert dialog
} else

if (response.ok) {
  const user = await response.json();
  alert('Registration successful!'); // Display a popup
}
    } catch (error) {
      // handle error
    }
  };

  return (
    <form onSubmit={handleSubmit}>
      <label>
        Username:
        <input type="text" value={username} onChange={(e) => setUsername(e.target.value)} required />
      </label>
      <label>
        Email:
        <input type="email" value={email} onChange={(e) => setEmail(e.target.value)} required />
      </label>
      <label>
        Password:
        <input type="password" value={password} onChange={(e) => setPassword(e.target.value)} required />
      </label>
      <button type="submit">Register</button>
      <button type="button" onClick={onClose}>Close</button> {/* call the onClose function when the close button is clicked */}
    </form>
  );
}

export default Register;